#order .promo-code form>.row:nth-child(2) {
    margin-top: 30px;
}

#order .promo-code form>.row:nth-child(3) {
    margin: 20px 0 40px;
}

#order .promo-code form>.row:nth-child(3) span:nth-child(2) {
    text-align: end;
}

#order .form-info>.row:nth-child(1) {
    margin-bottom: 20px;
}

#order .form-info .main-input {
    max-width: unset;
    height: 48px;
    outline: 1px solid var(--line-bg);
}

#order .form-info>.row.gap-10 {
    margin-top: 18px;
}

#order .form-info>.row.gap-20 {
    margin: 20px 0;
    container-type: inline-size;
}

#order .form-info>.row.gap-10+span {
    display: block;
    margin-top: 45px;
    margin-bottom: 15px;
}

#order form:not(.promo-code form) button {
    width: 100%;
    height: 48px;
    background-color: var(--black-secondary);
}

#order .form-info .row .main-input {
    width: calc(50% - 10px);
}

#order form .line {
    background-color: var(--line-bg);
    margin: 45px 0;
    height: 1px;
    width: 100%;
}

#order .form-delivery>.row:nth-child(2),
#order .form-delivery>.row:nth-child(3),
#order .form-payment>.row:nth-last-child(2),
#order .form-payment>.row:nth-last-child(3) {

    border: 1px solid var(--line-bg);
    padding: 13px 25px;
    border-radius: 20px;
}

#order .form-delivery>.row:nth-child(3) {
    margin-top: 10px;
}

#order .form-delivery+.row button,
#order .form-payment+.row button {
    cursor: pointer;
    margin-top: 25px;
}

#order form+.row button img {
    width: 11px;
    height: 11px;
}

#order .form-output {
    margin-bottom: 45px;
    border: 1px solid var(--line-bg);
    border-radius: 20px;
}

#order .form-output>.row>.row span,
#order .form-output>.row>.row b {
    text-decoration: none !important;
    text-decoration-line: unset !important;
    text-decoration-style: none !important;
}


#order .form-output .text-black-secondary {
    width: 120px;
}

#order .form-output .text-gray {
    text-decoration-line: underline;
}

#order .form-output>.row {
    margin: 0 25px;
    padding: 13px 0;
}

#order .form-output>.row:not(.row:nth-last-child(1)) {
    border-bottom: 1px solid var(--line-bg);
}

#order .form-delivery>span:nth-child(1),
#order .form-payment>span:nth-child(1) {
    margin-bottom: 20px;
    display: block;
}

#order .form-steps {
    margin-bottom: clamp(1.875rem, 1.4423rem + 1.9231vw, 3.75rem);
}

#order .form-steps span:nth-child(1), #order .form-steps span:nth-child(2),
#order p span,
#order .form-output .edit {
    user-select: none;
    cursor: pointer;

}

#order .form-info p {
    text-align: end;
}